.header {
	padding: 30px 0;
	background: $white;
	&.fixed {
		position: fixed;
		width: 100%;
		padding: 0;
		box-shadow: 0 5px 5px rgba(0, 0, 0, .2);
		z-index: 100;
		& .logo {
			transform: scale(0.7);
		}
	}
	@media (max-width: 1440px) {
		& .menu li {
			margin: 0 10px;
		}
	}
	@media (max-width: 1280px) {
		& .container {
			max-width: 100%;
		}
		& .menu li {
			margin: 0 10px;
		}
	}
}
.container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 91.66667%;
}
.menu {
	display: flex;
	& li {
		display: flex;
		align-items: center;
		margin: 0 40px 0 0;
	} 
	& li a {
		font-family: 'ZonaPro';
		font-size: 16px;
		font-weight: 700;
		color: #000;
		transition: .3s linear;
		&:hover {
			color: $green;
			transition: .3s linear;
		}
	}
}
.touch-menu {
    display: none;
	flex-direction: column;
	justify-content: center;
	width: 25px;
	height: 20px;
	cursor: pointer;
	display: none;
	z-index: 6;
	&:before {
		content: '';
		display: block;
		width: 25px;
		height: 4px;
		background: $text2;
		transition: all .3s ease 0s;
	}
	& span {
		display: block;
		width: 25px;
		height: 4px;
		margin: 4px 0;
		background: $text2;
		transition: all .3s ease 0s;
	}
	&:after {
		content: '';
		display: block;
		width: 25px;
		height: 4px;
		background: $text2;
		transition: all .3s ease 0s;
	}
}
.tel-select {
	width: 300px;
	font-family: 'ZonaPro';
	font-weight: 700;
	font-size: 22px;
	box-shadow: none;
	position: relative;
	&__header {
		width: 100%;
		margin: 1px 0 0;
		padding: 15px;
		font-weight: 700;
		z-index: 10;
		display: flex;
		justify-content: space-between;
	}
	&__body {
		line-height: 40px;
		font-weight: 700;
		border: 2px solid $green;
		border-radius: 7px;
		position: absolute;
		left: 15px;
		padding: 20px;
		background: $white;
		transition: .3s linear;
		visibility: hidden;
		opacity: 0;
		z-index: 10;
		&.active {
			visibility: visible;
			opacity: 1;
			transition: .3s linear;
		}
	}
	& a {
		color: $text2;
	}
}
.tel__icon {
	background: url(assets/img/arrow_white.png) 95% 48% no-repeat;
	width: 24px;
}