@media screen and (max-width: 1024px) {
	body.lock {
		overflow: hidden;
	}
	.wrapper {
		position: relative;
		z-index: 4;
	}
	.col-lg-6 {
		max-width: 100%;
		flex: 0 0 100%;
	}
	.hero {
	& .col-lg-3, & .col-lg-9 {
		flex: 0 0 100%;
		max-width: 100%;
	}
	}
	.header .menu {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		background: url(assets/img/hero-bg.jpg)  no-repeat;
		background-size: cover;
		padding: 120px 0 75px 150px;
		display: none;
		height: 100vh;
		overflow-y: scroll;
		z-index: 5;
		& li {
			line-height: 80px;
			margin: 0;
		}
		& li a {
			font-size: 36px;
			color: $white;
		}
		&.active {
			display: block;
		}
	}
	.menu .btn-green {
		margin: 50px 0;
	}
	.touch-menu {
	display: flex;
	position: absolute;
	right: 20px;
	top: 53px;
	z-index: 60;
	&.active {
		&:before {
			transform: rotate(45deg);
			top: 2px;
			transition: all .3s ease 0s;
			position: relative;
			background: $white;
		}
		& span {
			display: none;
			background: $white;
		}
		&:after {
			transform: rotate(-45deg);
			bottom: 2px;
			transition: all .3s ease 0s;
			position: relative;
			background: $white;
		}
	}
}
	.tel-select {
		width: 300px;
		margin: 0 80px 0 0;
		&__body {
			padding-right: 25px;
		}
	}
	.hero {
		min-height: 2000px;
		@media screen and (max-width: 767px) {
            min-height: 2930px;
        }
	}
	.hero__row {
    	flex-direction: column-reverse;
    }
    .hero__left {
    	padding: 0 20px;
    	margin: 0 0 450px;
    	text-align: center;
    	min-height: 1px;
    	& .btn-green {
    		margin: 0 15px;
    	}
    	& .btn-transparent {
    		margin: 0 0 0 15px;
    	}
    }
    .main-title {
    	text-align: center;
    	padding: 0 50px;
    }
    .row_pos {
    	bottom: -535px;
    	@media screen and (max-width: 767px) {
            bottom: -1465px;
        }
    	& .issues-wrap {
            &:after {
                top: 90%;
                @media screen and (max-width: 767px) {
           			 top: 95%;
        		}
            }
        }
    }
    .row_pos:after {
    	background-size: 100% 100%;
    	top: 75%;
    	left: -48%;
    }
    .issues {
    	flex-wrap: wrap;
    	padding: 0 30px;
    }
    .issue-item-wrap {
    	margin-bottom: 30px;
    }
    .img-wrap { 
    	& img {
    		top: 0;
    	}
     }
    .solutions {
    	padding: 190px 0 100px;
    }
     .solution-text {
     	padding: 35px 15px 50px;
     	& .sub-title {
     		font-size: 18px;
     	}
     }
     .options {
     	flex-direction: column;
     	&__left, &__right {
     		padding: 0 0 80%;
     	}
     	&__info {
     		padding: 0 150px;
     	}
     }
     .how-it-work {
     	padding: 150px 30px;
     	& .sign-block__body {
     		width: 95%;
     		&:after {
     			width: 100%;
     			left: 0;
     			background-size: 100% 100%;
     		}
     	}
     }
     .stages {
     	display: flex;
     }
    .stages_mobile {
     	display: none;
     }
     @media screen and (max-width: 740px) {
        .stages {display: none;}
        .stages_mobile {
        	display: block;
        	margin: 0 0 100px;
        	& .stages-item {
        		margin: 0 0 30px;
        	}
        	& .stages-item-img {
        		display: flex;
        		justify-content: space-between;
        	}
        	& .stages-img {
        		margin: 0 40px 30px 0;
        		position: relative;
        	}
        	& .img_fade {
        		position: absolute;
        		top: 0;
        		left: 0;

        	}
        	& img {
        		max-width: 100%;
        		height: auto;
        	}
        	& .sub-title {
        		display: flex;
        		align-items: center;
        	}
        	& .block-text {
        		text-align: left;
        		margin: 0;
        	}

        }
        .sign-block {
	    	padding: 80px 15px 100px;
	    	&__body {
	    		width: 120%;
	    		margin-left: -10%;
	    		&:after {
	    			background-size: 100% 84%;
	    			bottom: -10%;
	    		}
	    	}
	    	& .sub-recap {
				flex-direction: column;
			}
			& .recaptcha {
				margin: 0 0 30px;
			}
	    }
	    .sign-block-form {
	    	padding: 40px 60px;
	    }
	    .sign-block-form .sub-title {
	    	font-size: 28px;
	    	text-align: center;
	    }
    }
     .doctor-slider {
     	&__item {
     		padding: 75px 30px 70px 30px;
     		height: 1266px;
     	}
     	&__item-wrap {
     		flex-direction: column;
     	}
     }
     .doctor-img {
     	padding: 0 10px 50px;
     }
     .doctor-info {
		max-height: 550px;
		overflow: scroll;
	}
    .equipment-slider__item-wrap {
		flex-direction: column-reverse;
	}
	.equipment-slider__item .equipment-text {
		padding: 130px 90px 0;
	}
	.testimonials-slider_video {
		&__item {
			max-width: 710px;
			width: 100%;
			&.slick-center {
				min-width: 710px;
				min-height: 470px;
			}
			& img {
				max-width: 100%;
				height: auto;
			}
		}
	}
	.testimonials-slider_text {
		&__item {
			max-width: 710px;
			width: 100%;
			padding: 40px;
			&.slick-center {
				min-width: 710px;
				min-height: 490px;
			}
		}
	}
	.testimonials-slider_text__item::before,
    .testimonials-slider_video__item::before {
        content: none;
    }
	.testimonials-top {
		display: flex;
		margin: 0 0 15px;
		& .testimonials-img {
			width: 100px;
			height: 100px;
			border-radius: 50%;
			overflow: hidden;
			position: relative;
			z-index: -1;
		}
	}
     .sign-block-img {
     	display: none;
     	
     }
     .sign-block-form {
     	flex: 0 1 100%;
     	& .input-email {
     		margin: 0 0 25px;
     	}
     }
     .copyrights {
     	width: 35%;
     	
     }
     .social {
     	margin-top: -40px;
     }
     .web-masters {
     	width: 100%;
     	justify-content: flex-end;
     	margin-top: -24px;
     }
}


