footer{
  background: $blue;
  & .row {
    display: flex;
    justify-content: space-between;
  }
  & .content {
    max-width: 1735px;
    width: 100%;
    padding: 50px 20px;
  }
}
.copyrights {
  font-family: 'ZonaPro';
  line-height: 1.5;
  color: $textGray;
}
.social {
  & ul {
    display: flex;
  }
  & ul li {
    margin: 0 20px 0 0;
    width: 50px;
    height: 50px;
    &:hover {
        border-radius: 50%;
        background: linear-gradient(-45deg, $brand-2, $brand-1);
    }
  }
  & ul li a {
     width: 100%;
     height: 100%;
     display: block;
  }
  & a.instagram {
     background: url('assets/img/instagram-icon.png') center no-repeat;
  }
  & a.youtube {
     background: url('assets/img/youtube-icon.png') center no-repeat;
  }
  & a.facebook {
     background: url('assets/img/facebook-icon.png') center no-repeat;

  }
  & a.instagram:hover {
     background: url('assets/img/instagram-icon_hover.png') center no-repeat;
  }
  & a.youtube:hover {
     background: url('assets/img/youtube-icon_hover.png') center no-repeat;
  }
  & a.facebook:hover {
     background: url('assets/img/facebook-icon_hover.png') center no-repeat;
  }
}
.web-masters, .social, .copyrights {
  display: flex;
  align-items: center;
}
a.copy {
  margin: 0 0 0 auto;
  width: 263px;
  height: 40px;
  font-size: 12px;
  font-weight: 400;
  color: $white;
  display: flex;
  align-items: center;
  & p {
    font-family: 'ZonaPro';
    font-weight: 400;
    line-height: 1.5;
    color: $textGray;
  }
  & i {
    background: url('assets/img/kofelate-studio.png') right no-repeat;
    width: 24px;
    height: 24px;
    display: block;
    margin: 0 15px;
  }
  & span {
    font-weight: 700;
    color: $green2;
  }
}