html {
	height: 100%;
}
body {
	width: 100%;
	margin: 0;
	color: #252525;
	font: 16px Arial, Helvetica, sans-serif;
}
* {
	box-sizing: border-box;
}
input, textarea, select form,fieldset {
	vertical-align:middle;
	color:#000;
	margin:0;
	padding:0;
	border-style:none;
}
ul {
	margin:0;
	padding:0;
	list-style: none;
}
p, h1, h2, h3, h4, h5, h6, figure {
	margin: 0;
}
a, a:hover {
	text-decoration: none;
}
a:hover {
	color: $green2;
}
/****HERO BLOCK****/
.wrapper {
	overflow-x: hidden;
}
.col-sm-12, .col-lg-6 {min-height: auto;}
.hero {
	background: url(assets/img/hero-bg.jpg)  no-repeat;
	background-size: cover;
	width: 100%;
	position: relative;
	z-index: 0;
	&__content {
		max-width: 1530px;
		width: 100%;
		margin: 0 auto;
		padding: 0 0 120px 0;
		position: relative;
	}
	&__row {
		display: flex;
		
	}
	&__left {
		padding: 258px 0 187px 100px;
		min-height: 1px;
		& .btn {
			margin: 0 20px 0 0;
			@media screen and (max-width: 1280px) {
    			margin: 0 8px 0 0;
    		}
		}
		& .btn-transparent {
			margin: 0;
		}
	}
	&__right {
		background-size: 95%;
	}
	& p {
		margin: 0 0 65px 0;
		color: #a4b1c3;
		line-height: 28px;
		font-family: $font-family;
		font-size: 18px;
	}
}
.main-title {
	font-family: 'ZonaPro';
	font-weight: 700;
	font-size: 56px;
	color: $white;
	margin: 0 0 50px 0;
}
.btn {
	font-family: 'ZonaPro';
	font-weight: 700;
	line-height: 46px;
	padding: .375rem 29px;
	margin: 0;
	border-radius: .35rem;
}
.btn-green {
	background: $green;
	color: $white;
	transition: .3s linear;
	&:hover {
		background: $green2;
		transition: .3s linear;
	}
}
.btn-transparent {
	background: none;
	border: 2px solid $white;
	color: $white;
	&:hover {
		background: $white;
		color: $blue;
		transition: .3s linear;
	}
}
.row_pos2 {
	position: absolute;
	bottom: -465px;
	z-index: 5;
	& .issues-wrap {
		position: relative;
		z-index: 2;
		&:after {
			content: '';
			display: block;
			position: absolute;
			background: url(assets/img/issue-shadow.png) no-repeat;
			background-size: 100% 100%;
			width: 100%;
			height: 150px;
			top: 80%;
			left: 0;
			z-index: -1;
		}
	}
}
.issues {
	display: flex;
	justify-content: space-between;
}
.issue-grid {
	display: flex;
}
.issue-item-wrap {
	display: flex;
	min-height: 255px;
	margin: 0 0 45px
}
.issue-item-wrap.first {
	& .issue-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		position: relative;
		background: linear-gradient(-45deg, $brand-2, $brand-1);
		border-radius: .65rem;
	}
	& .issue-item:before {
		content: '';
		position: absolute;
		top: 12px; right: 12px; bottom: 12px; left: 12px;
		border-radius: .35rem;
		background: $white;
		z-index: 1;
	}
}
.issue-item {
	padding: 35px 30px 40px;
	min-height: 255px;
	background: $white;
	border-radius: .35rem;
	font-family: 'ZonaPro';
	font-size: 18px;
	font-weight: 700;
	line-height: 24px;
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 0 30px 0;
		min-height: 73px;
		position: relative;
		z-index: 2;
	}
	&__text {
		display: block;
		text-align: center;
		position: relative;
		z-index: 2;
	}
}
.content {
	max-width: 1170px;
	width: 100%;
	margin: 0 auto;
}

/****SOLUTIONS BLOCK****/
.solutions {
	padding: 550px 0 100px;
	background: $grey;
	& .btn-green {
		margin: 0;
	}
}
.title {
	font-family: 'ZonaPro';
	font-size: 52px;
	font-weight: 700;
	color: $text;
	text-align: center;
	margin: 0 0 35px;
}
.block-text {
	font-family: $font-family;
	font-size: 16px;
	line-height: 30px;
	color: $text2;
	text-align: center;
	letter-spacing: .5px;
	margin: 0 auto 75px;
}
.solutions__items {
	display: flex;
	flex-wrap: wrap;
	margin: 0 0 105px;
}
.solutions-item-wrap {
	display: flex;
	flex-direction: column;
	margin: 0 0 45px;
}
.solutions-item {
	background: $white;
	text-align: center;
	border-radius: 6px;
	flex: 1 1 auto;
}
.img-wrap {
	position: relative;
	padding: 0 0 56%;
	overflow: hidden;
	border-top-left-radius: 6px;
	border-top-right-radius: 6px;
	& img {
		position: absolute;
		top: 0;
		left: 0;
		max-width: 100%;
		height: auto;
	}
}
.solution-text {
	padding: 45px 60px 50px;
	font-family: $font-family;
	line-height: 24px;
	color: $text2;
}
.sub-title {
	font-family: 'ZonaPro';
	font-weight: 700;
	font-size: 20px;
	letter-spacing: .5px;
	color: $text;
	margin: 0 0 25px;
	&_big {
		font-family: 'ZonaPro';
		margin: 0 0 50px;
		font-weight: 700;
		font-size: 28px;
		text-align: center;
		position: relative;
	}
	&_big:before {
		content: '';
		position: absolute;
		background: url(assets/img/question-icon.png) no-repeat;
		width: 53px;
		height: 49px;
		top: -75px;
		left: 50%;
		margin-left: - 26px;
	}
}
.button {
	display: flex;
	justify-content: center;
}

/****OPTIONS BLOCK****/
.options {
	display: flex;
	position: relative;
	&__left {
		background-size: 150% auto;
		padding: 0 0 37%;
		position: relative;
		z-index: 1;
		&:before {
			content: '';
			background: #080d0e;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			opacity: .8;
			z-index: -1;
		}
	}
	&__right {
		padding: 0 0 37%;
		background-size: 180% auto;
		position: relative;
		z-index: 1;
		&:before {
			content: '';
			background: #080d0e;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			opacity: .8;
			z-index: -1;
		}
	}
	&__title1:before {
		content: '';
		position: absolute;
		width: 69px;
		height: 69px;
		top: -120px;
		left: 50%;
		margin-left: -35px;
	}
	&__title2:before {
		content: '';
		position: absolute;
		width: 86px;
		height: 63px;
		top: -120px;
		left: 50%;
		margin-left: -43px;
	}
	
	&__info {
		margin: 0 auto;
		color: $white;
		text-align: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -20%);
		& h3 {
			font-family: 'ZonaPro';
			font-weight: 700;
			font-size: 34px;
			margin: 0 0 35px;
			position: relative;
		}
		& p {
			line-height: 28px;
			font-family: $font-family;
			font-size: 18px;
			color: $text3;
		}
	}
}

/****HOW-IT-WORK BLOCK****/
.how-it-work {
	padding: 150px 0 150px;
	& .sign-block__body {
		display: block;
		width: 65%;
		text-align: center;
		margin: 0 auto;
		padding: 100px 50px 50px;
		position: relative;
		&:before {
			margin: -12px;
		}
		&:after {
			content: '';
			position: absolute;
			left: -35px;
			bottom: -27%;
			width: 860px;
			height: 125px;
			background: url(assets/img/easy-shadow.png) no-repeat;
			z-index: -1;
		}
		& .icon {
			&:before {
				background: url(assets/img/finger-click.png) 50% 50% no-repeat;
			}
		}
		& .sub-title_big {
			width: 100%;
			text-align: center;
			&:before {
				content: none;
			}
		}
	}
}
.stages {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 0 0 130px;
	position: relative;
	&__item {
		display: flex;
		& .block-text, & .sub-title {
			text-align: right;
			line-height: 1.5;
		}
		& .stages-item_txt {
			flex: 0 1 50%;
			padding: 50px 110px 50px 20px;
		}
		& .stages-item_img {
			display: flex;
			justify-content: center;
			margin: 0 0 90px;
			& .stages-img {
				display: flex;
				align-items: center;
				justify-content: center;
				flex: 0 1 60%;
				position: relative;
				overflow: hidden;
				& .img_fade {
					display: flex;
					align-items: center;
					justify-content: center;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					z-index: 2;
				}
			}
			&.col-6 {padding: 0;}
			& .stage-line_hrz {
				flex: 0 1 40%;
				position: relative;
			}
			& .stage-line_hrz__body {
				position: absolute;
				top: 50%;
				right: 0;
				width: 100%;
				height: 4px;
				background: $grey;
			}
			& .stage-line_hrz__body_fill {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				width: 0;
				height: 4px;
				background: $brand-2;
				transition: width 2s ease;
			}
		}
		&:nth-child(2n+1) .stage-line_hrz__body_fill {
				left: 0;
		}
		&:nth-child(2n) .stages-item_txt {
			padding: 50px 20px 50px 110px;
		}
		&:nth-child(2n) .block-text, &:nth-child(2n) .sub-title {
				text-align: left;
		}
	}
	& .col-12, {
		padding: 0;
	}
}
.line-wrap {
	width: 4px;
	background: $grey;
	position: absolute;
	top: 0;
	bottom: 0;
	z-index: 1;
	& .line {
		width: 100%;
		min-height: 40px;
		top: 0;
		left: 0;
		background: $brand-2;
		border-radius: 2px;
		transition: height 2s ease;
		position: relative;
		z-index: 2;
		&:after {
			content: '';
			position: absolute;
			left: -10px;
			bottom: -12px;
			width: 25px;
			height: 25px;
			border-radius: 50%;
			background: linear-gradient(-45deg, $brand-2, $brand-1);
			z-index: 5;
		}
	}
}
.icon {
	position: absolute;
	top: -70px;
	left: 50%;
	margin-left: -60px;
	width: 120px;
	height: 120px;
	border-radius: 50%;
	background: linear-gradient(-45deg, $brand-2, $brand-1);
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.stages_mobile {
	display: none;
}

/****DOCTOR BLOCK****/
.doctor {
	background: url(assets/img/hero-bg.jpg)  no-repeat;
	background-size: cover;
	padding: 150px 0 100px;
	& .content {
		max-width: 1340px;
	}
	& .title {
		color: $white;
	}
	& .block-text {
		color: $textGray;
	}
}
.doctor-slider {
	margin: 0 0 140px;
	& .slick-prev {
		left: -10%;
		transform: translate(0, -50%);
		@media screen and (max-width: 1440px) {
    		left: 0;
    	}
	}
	& .slick-next {
		right: -10%;
		transform: translate(0, -50%);
		@media screen and (max-width: 1440px) {
    		right: 0;
    	}
	}
	& .slick-dots {
		bottom: -80px;
	}
}
.doctor-slider__item {
		background: $white;
		padding: 80px 100px 60px 0;
		border-radius: 15px;
		height: 670px;
		margin: 0 30px;
		& .doctor-slider__item-wrap {
			display: flex;
		}
		& .doctor-img {
			display: flex;
			justify-content: center;
		}
		& img {
			width: auto;
			max-width: 100%;
			height: 100%;
		}
		& p {
			margin: 0 0 20px;
		}
	}
.doctor-info {
	height: 550px;
	overflow: auto;
	& .sub-title_big {
		text-align: left;
		&:before {
			content: none;
		}
	}
}
.intro-text {
	margin: 0 0 40px;
	padding: 10px 20px 10px 30px;
	font-family: 'ZonaPro';
	font-weight: 700;
	font-size: 18px;
	line-height: 30px;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 7px;
		height: 100%;
		background: linear-gradient(-45deg, $brand-2, $brand-1);
	}
	& p {
		margin: 0 0 20px;
	}
	& p:last-child {
		margin: 0;
	}
}
.main-text {
	font-family: $font-family;
	line-height: 1.5;
	color: $text2;
}

/****EQUIPMENT BLOCK****/
.equipment {
	padding: 140px 0 120px;
	background: $grey;
	& h2 {
		margin: 0 auto 35px;
	}
	
}
.equipment-slider {
	margin: 0 0 70px;
	& .slick-dots {
		bottom: -80px;
	}
	& .slick-prev {
		background: url(assets/img/slider-arrow_left_black.png) no-repeat;
		left: -13%;
		@media screen and (max-width: 1440px) {
    		left: -3%;
    	}
	}
	& .slick-next {
		background: url(assets/img/slider-arrow_right_black.png) no-repeat;
		right: -13%;
		@media screen and (max-width: 1440px) {
    		right: -3%;
    	}
	}
	&__item {
		
		& .equipment-slider__item-wrap {
			display: flex;
		}
		& .equipment-text {
			padding: 75px 90px 0 0;
		}
		& .sub-title_big {
			margin: 0 0 25px;
			text-align: left;
			line-height: 1.5;
			&:before {
				content: none;
			}
		}
		& .block-text {
			text-align: left;
		}
		& .equipment-img {
			display: flex;
			justify-content: center;
			max-height: 540px;
			& img {
			width: auto;
			max-width: 100%;
			height: 100%;
			}
		}
	}
}

/****ADVANTAGES BLOCK****/
.advantages {
	background: url(assets/img/hero-bg.jpg)  no-repeat;
	background-size: cover;
	padding: 150px 20px 80px;
	& .title {
		color: $white;
	}
	& .block-text {
		color: $textGray;
	}
	& .video {
		margin: 0 auto 80px;
		border-radius: 10px;
		padding: 0;
		max-height: 580px;
		overflow: hidden;
		position: relative;
		& img {
			width: 100%;
			height: auto;
		}
	}
	& .icon {
		top: 50%;
		transform: translate(0, -50%);
		&:before {
			background: url(assets/img/play-icon.png) 50% 50% no-repeat;
		}
		&:hover:before {
			background: url(assets/img/play-icon_big.png) 50% 50% no-repeat;
		}
	}
}
.advantages-items {
	font-family: 'ZonaPro';
	font-weight: 700;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
.advantages-item {
	flex: 0 1 27%;
	margin: 0 0 70px;
}
.advantages-number {
	font-size: 118px;
	text-align: center;
	background: linear-gradient(-45deg, $brand-2, $brand-1);
	background-position: -20% 0;
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	transition: .3s linear;
	&:hover {
		transform: scale(1.1);
		transition: .3s linear;
	}
}
.advantages-text {
	text-align: center;
	color: $white;
	line-height: 1.5;
}

/****TESTIMONIALS BLOCK****/
.testimonials {
	background: $brand;
	padding: 150px 0 200px;
	& .title {
		color: $white;
	}
	& .block-text {
		color: $textGray;
		margin-bottom: 40px;
	}
	& .btn {
		line-height: 38px;
	}
	& .btn-green {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 9px 7px 0 7px;
			border-color: $green transparent transparent transparent;
			display: none;
			transition: .3s linear;
		}
	}
	& .btn-green.active:before {
		display: block;
	}
	& .btn-green:hover:before {
		border-color: $green2 transparent transparent transparent;
	}
}
.testimonials-tabs {
	display: flex;
	justify-content: center;
	margin: 0 0 70px;
}
.btn-blue {
	margin: 0 30px 0 0;
	background: #115894;
	color: $white;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translateX(-50%);
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 9px 7px 0 7px;
		border-color: #115894 transparent transparent transparent;
		display: none;
	}
	&.active:before {
		display: block;
	}
}
.testimonials-slider_text {
	display: flex;
	align-items: center;
	flex-direction: column;
	visibility: hidden;
	height: 0;
	&.active {
		display: flex;
		visibility: visible;
		height: auto;
	}
	&__item {
		max-width: 945px;
		width: 100%;
		max-height: 490px;
		margin: 0 50px;
		padding: 70px 100px 80px;
		background: $white;
		border-radius: 10px;
		text-align: center;
		position: relative;
		z-index: 1;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: $black;
			border-radius: 10px;
			opacity: .5;
		}
		&.slick-center {
			min-width: 945px;
			min-height: 555px;
			&:before {
				content: none;
			}
		}
	}
}
.testimonials-top {
	display: flex;
	margin: 0 0 40px;
	& .testimonials-img {
		width: 100px;
		height: 100px;
		border-radius: 50%;
		overflow: hidden;
		position: relative;
		z-index: -1;
	}
	& .testimonials-info {
		padding: 20px 20px 20px 30px;
		& strong {
			display: block;
			font-family: 'ZonaPro';
			font-size: 20px;
			margin: 0 0 15px;
		}
		& span {
			display: block;
			text-align: left;
			color: #a8a8a8;
		}
	}
	
}
.testimonials-bottom {
	& .block-text.left {
		text-align: left;
		color: $text2;
	}
}
.slick-prev
{
	background: url(assets/img/slider-arrow_left.png)  no-repeat;
}
.slick-next
{
	background: url(assets/img/slider-arrow_right.png)  no-repeat;
}
.testimonials-slider_video {
	display: flex;
	align-items: center;
	flex-direction: column;
	visibility: hidden;
	height: 0;
	&.active {
		display: flex;
		visibility: visible;
		height: auto;
	}
	&__item {
		max-width: 945px;
		width: 100%;
		max-height: 490px;
		margin: 0 50px;
		background: $white;
		border-radius: 10px;
		position: relative;
		overflow: hidden;
		z-index: 1;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: $black;
			border-radius: 10px;
			opacity: .5;
		}
		& .icon {
			display: none;
		}
		&.slick-center {
			min-width: 945px;
			min-height: 555px;
			&:before {
				content: none;
			}
			.icon {
				display: block;
				top: 50%;
				transform: translate(0, -50%);
				&:before {
					background: url(assets/img/play-icon.png) 50% 50% no-repeat;
				}
			}
			& .icon:hover:before {
				background: url(assets/img/play-icon_big.png) 50% 50% no-repeat;
			}
		}
	}
}

/****SIGN-BLOCK BLOCK****/
.sign-block {
	padding: 150px 30px 170px;
	background: $grey;
	position: relative;
	z-index: 1;
	& .content {
		max-width: 1340px;
		width: 100%;
	}
	&__body {
		display: flex;
		position: relative;
		background: $white;
		border-radius: 6px;
		position: relative;
		&:after {
			content: '';
			background: url(assets/img/issue-shadow.png) 0 100% no-repeat;
			background-size: 100% 100%;
			display: block;
			width: 100%;
			height: 150px;
			position: absolute;
			bottom: -13%;
			left: 0;
			z-index: -1;
		}
	}
	&__body:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: -25px;
		background: linear-gradient(-45deg, $brand-2, $brand-1);
		border-radius: 12px;
		z-index: -1;
	}
}
.sign-block-img {
	flex: 0 1 45%;
	& img {
		max-width: 100%;
		height: auto;
	}
}
.sign-block-form {
	flex: 0 1 55%;
	padding: 80px 70px 60px 80px;
	& .sub-title {
		font-size: 36px;
		margin: 0 0 50px;
		padding: 0 15px;
	}
	& form {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	& input, textarea {
		border: 1px solid #e4e4e9;
		border-radius: 7px;
		padding: 15px;
		color: $text2;
	}
	& input:focus, textarea:focus  {
		border: 2px solid $green;

	}
	& input[type="submit"] {
		padding: 0 29px;
		line-height: 59px;
		color: $white;
	}
	& input::-webkit-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
	& input::-moz-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
	& input:-ms-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}

	& textarea::-webkit-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
	& textarea::-moz-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
	& textarea:-ms-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}

	& input:focus::-webkit-input-placeholder { color:transparent;}
	& input:focus::-moz-placeholder { color:transparent;}
	& input:focus:-ms-input-placeholder { color:transparent;}

	& textarea:focus::-webkit-input-placeholder { color:transparent;}
	& textarea:focus::-moz-placeholder { color:transparent;}
	& textarea:focus:-ms-input-placeholder { color:transparent;}
	& input:focus, & textarea:focus {
		outline: none;
	}
	input:focus {
		position: relative;
		&:before {
			content: 'Ваше имя';
			position: absolute;
			width: 50px;
			height: 20px;
			top: -10px;
			left: 50px;
			background: $white;
			padding: 4px;
		}
	}
	& .input-name, & .input-comment {
		width: 100%;
	}
	& .input-phone, & .input-email {
		width: 100%;
	}
	& .comment {
		margin: 0 0 30px;
	}
	& .sub-recap {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
.order-form label {
	display: inline;
}
label span.wpcf7-not-valid-tip {
	color: #bd6c6c;
	opacity: 1;
}
.name, .phone, .comment, .select {
	margin: 0 0 10px;
}
.name label span, .phone label span, .email label span, .comment label span {
  position: absolute;
  left: 40px;
  top: -6px;
  font-size: 12px;
  color: $green;
  background: $white;
  padding: 0 4px;
  display: none;
}
.deprt-select {
	width: 100%;
	height: 50px;
	line-height: 48px;
	border: 1px solid #e4e4e9;
	border-radius: 7px;
	font-family: 'ZonaPro';
	font-size: 16px;
	transition: all 0s;
	position: relative;
	& .list {
		width: 100%;
		background: $white;
		border: 2px solid $green;
		margin: 1px 0 0;
		padding: 20px 20px;
	}
	& .current {
		font-weight: 400;
	}
	& .option {
		line-height: 40px;
	}
	& .option:hover {
		color: $green;
	}
	&.open {
		border: 2px solid $green;
		transition: all 0s;
		&::after {
			transform: rotate(180deg);
		}
	}
	&::after {
	    content: '';
	    font-family: 'FontAwesome';
	    display: block;
	    width: 11px;
	    height: 7px;
	    margin-top: -4px;
	    pointer-events: none;
	    position: absolute;
	    right: 32px;
	    top: 50%;
	    background: url('./assets/img/arrow_black.png') center no-repeat;
	}
}

///***INFO-BLOCK****///
.info-block {
	padding: 100px 20px;
	&__box {
		height: 357px;
		padding: 0 40px;
		overflow: auto;
	}
	& .sub-title-big {
		text-align: left;
		&:before {
			content: none;
		}
	}
	& .block-text {
		text-align: left;
		margin: 0 0 30px;

	}
}
.info-block__box::-webkit-scrollbar {
    width: 7px;
}

.info-block__box::-webkit-scrollbar-thumb {
    background-color: $green2;
    background-clip: padding-box;
    border: 0px solid #f9f9f9;

    &:hover {
        background-color: #41a6f4;
    }
}

.info-block__box::-webkit-scrollbar-track {
    background-color: #f9f9f9;
    cursor: pointer;
}

::-webkit-scrollbar-button {
    background-color: transparent;
}

/* IE */
.info-block__box {
    scrollbar-face-color: $green2;
    scrollbar-shadow-color: #f9f9f9;
    scrollbar-highlight-color: #f9f9f9;
}

/* FireFox */
.info-block__box {
    scrollbar-color: $green2 #f4f6f7;
}


