@media screen and (max-width: 575px) {
    .header {
    	padding: 10px 0;
    	.container {
    		padding: 0;
    	}
    }
    .logo {
    	transform: scale(0.7);
    }
    .tel__icon {
    	background: url(assets/img/phone1.png)  no-repeat;
    	width: 21px;
    	height: 24px;
    	margin-right: 90px;
    	cursor: pointer;
    	@media screen and (max-width: 320px) {
    		margin-right: 110px;
    	}
    }
    .tel-select {
    	width: auto;
    	margin: 0 0 0 30px;
    	@media screen and (max-width: 320px) {
    			margin: 0;
    		}
    	&__header {
    		background: none;
    	}
    	&__body {
    		left: -100%;
    		@media screen and (max-width: 320px) {
    			left: -120%;
    		}
    	}
        &__current {
            display: none;
        }
    }
    .header .menu {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background: url(assets/img/hero-bg.jpg)  no-repeat;
		background-size: cover;
		padding: 50px 15px;
		display: none;
		z-index: 5;
		& li {
			line-height: 40px;
			
		}
		& li a {
			font-size: 18px;
			color: $white;
		}
		&.active {
			display: block;
		}
	}
	.menu .btn-green {
		width: 100%;
		margin: 35px 0;
	}
	.touch-menu {
		top: 33px;
	}
    .hero {
    	min-height: 2730px;
        @media screen and (max-width: 320px) {
                min-height: 2830px;
            }
    }
    .main-title {
    	font-size: 26px;
    	margin: 0 0 30px;
    	padding: 0 10px;
    }
    .hero__content {
        position: static;
    }
    .hero p {
    	margin: 0 0 30px;
    }
    .hero__left {
    	& .btn-transparent {
    		margin: 0;
    		width: 100%;
    	}
    	& .btn-green {
    		margin: 0 0 30px;
    		width: 100%;
    	}
    }
    .row_pos {
        bottom: -70px;
        @media screen and (max-width: 374px) {
            bottom: -170px;
        }
    	& .issues-wrap {
            position: relative;
            z-index: 2;
            &:after {
                top: 95%;
            }
        }
    }
    .issues {
    	padding: 0 20px;
    }
    .issue-item-wrap {
    	flex: 0 1 100%;
    }
    .title {
    	font-size: 24px;
    }
    .sub-title_big {
    	font-size: 22px;
    	margin: 0 0 25px;
    }
    .block-text {
    	margin: 0 0 34px;
    	line-height: 1.4;
    }
    .solutions {
    	padding: 150px 0 75px;
    	@media screen and (max-width: 374px) {
    		padding-top: 195px;
    	}
    }
    .options__left, .options__right {
    	padding: 0 0 130%;
    }
    .options__left {
    	background-position: 0 0;
    	background-size: 200%; 
    }
    .options__info {
    	padding: 0 15px;
    	top: 45%;
    	& h3 {
    		font-size: 24px;
    		margin: 0 0 10px;
    	}
    }
    .options__title1:before, .options__title2:before {
    	background-size: 70%;
    	top: -80px;
    	background-position: center;
    }
    .how-it-work {
    	padding: 75px 15px 75px;
    }
    .stages {
    	display: none;
    }
    .stages__left .stages-item_txt,
    .stages__right .stages-item_txt {
    	padding: 0;
    }
    .line-wrap,
    .stages__left .stages-item_img .stage-line_hrz,
    .stages__right .stages-item_img .stage-line_hrz {
    	display: none;
    }
        /*.stages {display: none;}*/
        .stages_mobile {
        	display: block;
        	/*& .stages-item {
        		margin: 0 0 30px;
        	}
        	& .stages-item-img {
        		display: flex;
        	}*/
        	& .stages-img {
        		margin: 0;
        		position: relative;
                width: 21%;
        	}
        /*	& .img_fade {
        		position: absolute;
        		top: 0;
        		left: 0;

        	}*/
            & h3 {
                width: 70%;
            }
        	& img {
        		width: 134%;
        		height: auto;
        	}
        	/*& .block-text {
        		text-align: left;
        	}*/

        }
    
    .how-it-work .sign-block__body {
    	padding: 100px 15px 40px;
    	margin-top: 120px;
    	&:after {
    		left: 0;
    		width: 100%;
    		background-size: 20%;
    		
    	}
    }


    .doctor {
    	padding: 40px 0 50px;
    	& .button {
    		padding: 0 15px;
    	}
    	& .btn {
    		width: 100%;
    	}
    }
    .doctor-slider {
        &__item {
            padding: 35px 5px 30px;
            height: 1266px;
        }
    }
    .doctor-info {
        max-height: 810px;
        overflow: scroll;
    }
    .doctor-info .sub-title_big {
    	font-size: 20px;
    }
    .intro-text {
    	font-size: 16px;
    }
    .equipment {
    	padding: 50px 15px;
    }
    .equipment-slider__item .equipment-text {
            padding: 15px 0 0 0;
        }
    .advantages {
    	padding: 50px 15px;
    }
    .advantages-items-row {
    	flex-wrap: wrap;
    }
    .advantages-item {
    	flex: 0 1 45%;
    }
    .advantages .icon { 
        transform: scale(0.7);
        margin-top: -50px;
    }
    .testimonials {
    	padding: 50px 15px 80px;
    	& .btn {
    		width: 140px;
    		height: 70px;
    		display: block;
    		margin: 0 7px ;
    		padding: 15px;
    		white-space: pre-wrap;
    		line-height: 18px;
    	}
    }
    .testimonials-tabs {
    	margin: 0 0 35px;
    }

    .testimonials-slider_video__item, 
    .testimonials-slider_video__item.slick-center,
    .testimonials-slider_text__item,
    .testimonials-slider_text__item.slick-center {
    	max-width: 550px;
    	max-height: 265px;
    	min-width: 550px;
    	min-height: 265px;
    }
    .testimonials-slider_text__item,
    .testimonials-slider_text__item.slick-center {
    	max-width: 550px;
    	max-height: 265px;
    	min-width: 550px;
    	min-height: 265px;
        width: 100%;
    	padding: 15px 100px 15px 115px;
    	border-radius: 10px;
    }
    .testimonials-slider_video__item.slick-center .icon {
    	transform: scale(0.7);
    	margin-top: -52px;
    }
    .testimonials-top {
    	margin: 0;
    }
    .testimonials-top .testimonials-img {
    	width: 60px;
    	height: 60px;
    	margin: 20px 20px 20px 0;
    }
    .testimonials-top .testimonials-info {
    	width: 65%;
    	padding: 20px 0;
    	& strong {
    		text-align: left;
    	}
    }
    .testimonials-bottom {
        height: 110px;
        padding: 0 15px 0 0;
        overflow-y: scroll;
    }
    .testimonials-bottom .block-text {
        margin: 0;
    }
    .testimonials-slider_text__item::before,
    .testimonials-slider_video__item::before {
        content: none;
    }
    .slick-dots {
    	bottom: -50px;
    }
    .sign-block-form {
    	padding: 40px 30px;
    }
    .info-block {
    	padding: 50px 15px;
    	&__box {
    		padding: 0 15px 0 5px;
    	}
    	& .sub-title_big {
    			font-size: 20px;
    			text-align: left;
    	}
    }
    footer {
    	padding: 0 15px;
    	& .logo  {
    		width: 100%;
    		text-align: center;
    		margin: 0 0 30px;
    	}
    	& .copyrights {
    		margin: 0 0 15px;
    	}
    	& .social {
    		margin: 0 0 15px;
    	}
    	& .web-masters, .social, .copyrights {
    		justify-content: center;
    		width: 100%;
    	}
    	& .web-masters {
    		margin: 0;
    	}
    	& a.copy {
    		display: flex;
    		justify-content: center;
    		margin: 0; 
    	}
    	& a.copy i {
    		background: url("assets/img/kofelate-studio_white.png") right no-repeat;
    		width: 18px;
    		height: 16px;
    		margin: 0 10px;
    	}
    	& a.copy span {
    		color: $white;
    	}
    }
    .modal .title {
    	font-size: 24px !important;
    }
    .modal-content {
    	padding: 30px 10px;
    }
    .modal .deprt-select.open {
    	border: 2px solid $green;
    }
}
