.modal {
    display: none;
    background: rgba(0, 0, 0, .8);
    &.show {
        display: block;
    }
    & .title {
    font-size: 44px !important;
    text-align: center !important;
    width: 100%;
    }
    & .sub-recap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        @media screen and (max-width: 575px) {
             justify-content: center;
             & .recaptcha {
                margin: 0 0 30px;
            }
         }
     }
    
}
.modal-open .modal {
    padding-right: 0 !important;
}

.modal-dialog {
    display: flex;
    align-items: center;
    max-width: 750px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: -25px;
        background: linear-gradient(-45deg, $brand-2, $brand-1);
        border-radius: 12px;
        z-index: -1;
    }
    @media screen and (max-width: 1024px) {
        max-width: 700px;
    }
}

.modal-header {
    border: none;
    padding-bottom: 0;
}
.modal-body {}
.row {
    display: flex;
    flex-wrap: wrap;
}
.modal-content {
    border-radius: 0;
    border: none;
    padding: 30px 40px;
    @media screen and (max-width: 767px) {
        overflow-y: scroll;
        height: 94vh;
    }
}
.close {
    position: absolute;
    right: 3px;
    top: 0;
    outline: none !important;
    z-index: 1;
    background: linear-gradient(-45deg, #00d0c4, #00d7bc) !important;
    border-bottom-left-radius: 12px;
    opacity: 1;
    &:hover {
        opacity: 1 !important;
        svg {
            transform: rotate(180deg);
            transition: .3s linear;
        }
    }
}
.deprt-select-modal {
    width: 100%;
    height: 50px;
    line-height: 48px;
    border: 1px solid #e4e4e9;
    border-radius: 7px;
    font-family: 'ZonaPro';
    font-size: 16px;
    & .list {
        width: 100%;
        background: $white;
        border: 2px solid $green;
        margin: 1px 0 0;
    }
    & .current {
        font-weight: 400;
    }
    & .option {
        line-height: 40px;
    }
    & .option:hover {
        color: $green;
    }
    &.open {
        border: 2px solid $green;
        &::after {
            transform: rotate(180deg);
        }
    }
    &::after {
        content: '';
        font-family: 'FontAwesome';
        display: block;
        width: 11px;
        height: 7px;
        margin-top: -4px;
        pointer-events: none;
        position: absolute;
        right: 32px;
        top: 50%;
        background: url('./assets/img/arrow_black.png') center no-repeat;
    }
}

span.center {text-align: center;}

.modal input, .modal textarea {
        border: 1px solid #e4e4e9 !important;
        border-radius: 7px;
        padding: 15px !important;
        color: $text2;
        width: 100%;
}
span.wpcf7-not-valid-tip {
    padding: 0 0 0 5px;
    font-size: 12px;
    color: #bd6c6c;
    opacity: 0;
}
input.wpcf7-not-valid-tip, textarea.wpcf7-not-valid-tip {
    border: 2px solid #bd6c6c;
}
.modal input:focus, .modal textarea:focus{
    border: 2px solid $green !important;
    outline: none;
}

.modal {
    & input::-webkit-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
    & input::-moz-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
    & input:-ms-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}

    & input:focus::-webkit-input-placeholder { color:transparent;}
    & input:focus::-moz-placeholder { color:transparent;}
    & input:focus:-ms-input-placeholder { color:transparent;}

    & textarea::-webkit-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
    & textarea::-moz-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}
    & textarea:-ms-input-placeholder {font-family: 'ZonaPro'; color: $text2; opacity: 1;}

    & textarea:focus::-webkit-input-placeholder { color:transparent;}
    & textarea:focus::-moz-placeholder { color:transparent;}
    & textarea:focus:-ms-input-placeholder { color:transparent;}
    & input[type="submit"] {
        line-height: 30px;
        width: 240px;
        color: $white;
    }
    & .btn {
        border-radius: 7px;
    }
}
.modal .order-form label {
    display: inline;
}

